	module.exports = {"data":{"post":{"html":"<p>BBC says <a href=\"http://news.bbc.co.uk/1/hi/world/europe/4258376.stm\" title=\"BBC NEWS | World | Europe | 'Slim victory' for German right\">'Slim victory' for German right</a>, but I would say that Schr&ouml;der and Merkel both lost. Both SPD and Union lost several percent compared to 2002, neither Red/Green nor Block/Yellow has a majority. The big winners are the neo-liberal FDP (over 10%) and the neo-socialist Left/PDS (over 8%). The most likely result is a grand coalition of Union and SPD, a coalition of losers. </p>\n<p>Heiko wonders about a <a href=\"http://www.hebig.com/archives/003369.shtml\" title=\"Jamaica coalition by Heiko Hebig | hebig.com\">Jamaica coalition</a> of Union/FDP/Greens. That would be a mathematical possibility, but is practically impossible.</p>","fields":{"title":"Nobody Wins","date":"18 Sep 2005","url":"https://papascott.de/2005-09-18-nobody-wins/"}}},"pathContext":{"slug":"/2005-09-18-nobody-wins/","prev":{"fields":{"title":"Teppich Angie","date":"17 Sep 2005","slug":"/2005-09-17-teppich-angie/"}},"next":{"fields":{"title":"Who's On First?","date":"18 Sep 2005","slug":"/2005-09-18-whos-on-first/"}}}}

